import React, { useEffect, useRef, useState } from 'react';
import * as echarts from 'echarts';

interface SimpleChartProps {
  data: any;
  type: 'line' | 'pie';
  height?: number;
  width?: string;
}

const SimpleChart: React.FC<SimpleChartProps> = ({ data, type, height = 400, width = '100%' }) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstance = useRef<echarts.ECharts | null>(null);
  const [isReady, setIsReady] = useState(false);

  // ÂàùÂßãÂåñÂõæË°®
  useEffect(() => {
    if (chartRef.current && !chartInstance.current) {
      try {
        chartInstance.current = echarts.init(chartRef.current);
        setIsReady(true);
        console.log('‚úÖ ÂõæË°®ÂàùÂßãÂåñÊàêÂäü');
      } catch (error) {
        console.error('‚ùå ÂõæË°®ÂàùÂßãÂåñÂ§±Ë¥•:', error);
      }
    }

    return () => {
      if (chartInstance.current) {
        chartInstance.current.dispose();
        chartInstance.current = null;
      }
    };
  }, []);

  // Êõ¥Êñ∞ÂõæË°®Êï∞ÊçÆ
  useEffect(() => {
    if (!isReady || !chartInstance.current || !data) {
      return;
    }

    try {
      console.log('üî• ÂºÄÂßãÊõ¥Êñ∞ÂõæË°®Êï∞ÊçÆ:', data);
      
      let option: any;
      
      if (type === 'line') {
        option = {
          title: { text: 'ÊâßË°åË∂ãÂäøÂõæ', left: 'center' },
          tooltip: { trigger: 'axis' },
          legend: { data: ['ÊàêÂäü', 'Â§±Ë¥•', 'ÊÄªËÆ°'], top: 30 },
          grid: { left: '3%', right: '4%', bottom: '3%', containLabel: true },
          xAxis: { type: 'category', data: data.dates || [] },
          yAxis: { type: 'value' },
          series: [
            {
              name: 'ÊàêÂäü',
              type: 'line',
              data: data.successCounts || [],
              color: '#00A65A',
            },
            {
              name: 'Â§±Ë¥•',
              type: 'line',
              data: data.failedCounts || [],
              color: '#c23632',
            },
            {
              name: 'ÊÄªËÆ°',
              type: 'line',
              data: data.totalCounts || [],
              color: '#F39C12',
            },
          ],
        };
      } else if (type === 'pie') {
        option = {
          title: { text: 'ÊâßË°åÁä∂ÊÄÅÂàÜÂ∏É', left: 'center' },
          tooltip: { trigger: 'item', formatter: '{b} : {c} ({d}%)' },
          legend: { orient: 'vertical', left: 'left', top: 'middle' },
          series: [
            {
              name: 'ÊâßË°åÁä∂ÊÄÅ',
              type: 'pie',
              radius: '50%',
              data: [
                { value: data.successExecutions || 0, name: 'ÊàêÂäü' },
                { value: data.failedExecutions || 0, name: 'Â§±Ë¥•' },
                { value: data.runningJobs || 0, name: 'ËøêË°å‰∏≠' },
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)',
                },
              },
            },
          ],
          color: ['#00A65A', '#c23632', '#F39C12'],
        };
      }

      if (option) {
        // Ê∏ÖÁ©∫Áé∞ÊúâÈÖçÁΩÆ
        chartInstance.current.clear();
        
        // ËÆæÁΩÆÊñ∞ÈÖçÁΩÆ
        chartInstance.current.setOption(option, true);
        
        // Âº∫Âà∂ÈáçÁªò
        chartInstance.current.resize();
        
        console.log('‚úÖ ÂõæË°®Êõ¥Êñ∞ÊàêÂäü');
        
        // È™åËØÅÈÖçÁΩÆ
        setTimeout(() => {
          const currentOption = chartInstance.current?.getOption();
          console.log('üî• ÂõæË°®ÈÖçÁΩÆÈ™åËØÅ:', currentOption);
          
          if (currentOption && currentOption.series && Array.isArray(currentOption.series)) {
            const hasData = currentOption.series.some(series => 
              series.data && Array.isArray(series.data) && series.data.length > 0
            );
            console.log('üî• ÂõæË°®ÊúâÊï∞ÊçÆ:', hasData);
          }
        }, 100);
      }
    } catch (error) {
      console.error('‚ùå ÂõæË°®Êõ¥Êñ∞Â§±Ë¥•:', error);
    }
  }, [data, type, isReady]);

  // Á™óÂè£Â§ßÂ∞èÂèòÂåñÊó∂ÈáçÊñ∞Ë∞ÉÊï¥
  useEffect(() => {
    const handleResize = () => {
      if (chartInstance.current) {
        chartInstance.current.resize();
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div
      ref={chartRef}
      style={{
        width,
        height,
        border: '1px solid #f0f0f0',
        borderRadius: '4px',
        backgroundColor: '#fff',
        visibility: 'visible',
        display: 'block',
        opacity: 1,
        position: 'relative',
        zIndex: 1,
      }}
    />
  );
};

export default SimpleChart;
